// src/components/BurgerMenu/BurgerMenu.scss

.burger-menu-container {
    position: relative;
    z-index: 1000;
    top: 5px;
    
    @media (max-width: 768px) {
      display: block;
    }
  }
  
  .burger-icon {
    cursor: pointer;
    z-index: 1001;
    position: relative;
    width: 30px;
    height: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    
    span {
      display: block;
      width: 30px;
      height: 3px;
      background: #fff;
      transition: var(--transition-speed);
      border-radius: 2px;
    }
    
    &.active {
      span {
        &:nth-child(1) {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%) rotate(45deg);
            transition: var(--transition-speed);
            width: 100%;
          }
          
          &:nth-child(2) {
            opacity: 0;
          }
          
          &:nth-child(3) {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%) rotate(-45deg);
            transition: var(--transition-speed);
            width: 100%;
          }
      }
    }
  }
  
  .burger-menu {
    position: fixed;
    height: 100vh;
    width: 380px;
    background-image: url('../../assets/images/mob-menu-background.jpg');
    background:  lightgray -920.802px -84.901px / 412.977% 126.995% no-repeat, linear-gradient(0deg, rgba(0, 0, 0, 0.20) 0%, rgba(0, 0, 0, 0.20) 100%), linear-gradient(110deg, #020D1F 3.16%, #0F264F 96.53%);
    background-size: cover;
    background-position: center;
    background-color: rgba(0, 0, 0, 0.95);
    flex-direction: column;
    display: flex; // Всегда используем flex
    transform: translateX(-330px); // Начальная позиция за пределами экрана
    transition: var(--transition-speed); // Применяем переход
    z-index: 1000;
    top: -30px;
    left: 0;
    display:none;
   
    
    &.open {
      transform: translateX(0);
     
      top: -30px;
      left: -330px;
      width: 380px;
      display: flex;
     
    }
    
    .close-button {
      position: absolute;
      top: 20px;
      right: 20px;
      background: transparent;
      border: none;
      cursor: pointer;
      
      img {
        width: 24px;
        height: 24px;
      }
    }
    
    .menu-content {
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      padding: 60px 30px 40px;
    }
    
    .name-section {
      margin-bottom: 40px;
      
      h2 {
        font-size: 20px;
        color: #fff;
        margin: 0;
        font-weight: normal;
      }
      
      h1 {
        font-size: 36px;
        color: #4ECDC4;
        margin: 0;
        font-weight: bold;
      }
    }
    
    .burger-nav {
      display: flex;
      flex-direction: column;
      margin-bottom: 40px;
      
      .nav-link {
        color: #fff;
        font-size: 18px;
        margin: 15px 0;
        text-decoration: none;
        position: relative;
        
        &:after {
          content: '';
          position: absolute;
          width: 0;
          height: 2px;
          bottom: -5px;
          left: 0;
          background-color: #fff;
          transition: width 0.3s ease;
        }
        
        &:hover:after {
          width: 100%;
        }
      }
    }
    
    .burger-menu-footer {
      margin-top: auto;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      
      .contact-button {
        background-color: transparent;
        color: #fff;
        border: 2px solid #fff;
        padding: 10px 24px;
        border-radius: 30px;
        font-size: 16px;
        cursor: pointer;
        transition: 0.3s ease;
        margin-bottom: 30px;
        
        &:hover {
          background-color: #fff;
          color: #000;
        }
      }
      
      .social-icons {
        display: flex;
        gap: 20px;
        
        .social-icon {
          display: flex;
          justify-content: center;
          align-items: center;
          transition: 0.3s ease;
          
          img {
            width: 24px;
            height: 24px;
          }
          
          &:hover {
            transform: translateY(-5px);
          }
        }
      }
    }
  }
  .no-scroll {
    overflow: hidden;
}